<template>
  <div>
    <base-panel>
      <template slot="panel-head">
        <router-link to="/">/主页</router-link>
        /
        <span>登录</span>
      </template>
      <template slot="panel-content">
        <el-form
          :model="ruleForm"
          status-icon
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="账号" prop="username">
            <el-input
              type="text"
              v-model="ruleForm.username"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="token">
            <el-input
              type="password"
              v-model="ruleForm.token"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="login">提交</el-button>
            <el-button>重置</el-button>
          </el-form-item>
        </el-form>
      </template>
    </base-panel>
  </div>
</template>

<script>
import basePanel from "../components/layout/basePanel.vue";
import {login} from '../plugins/api'
export default {
  components: { basePanel },
  data() {
    return {
      ruleForm: {
        username: "",
        token: "7de298fc-2bae-4afb-b20b-ca85130e4159",
      },
      rules: {
        username: [
          { required: true, message: "请输入用户名字", trigger: "blur" },
        ],
        token: [
          { required: true, message: "请输入用户名字", trigger: "blur" }
          ],
      },
    };
  },
  methods: {
    login() {
      this.$refs.ruleForm.validate(async (valid) => {
        if(valid){
          const res = await login(this.ruleForm.token)
          console.log(res);
          sessionStorage.setItem('userInfo',JSON.stringify(res))
          sessionStorage.setItem('accesstoken',this.ruleForm.token)
          this.$router.push('/')
          this.$message({
          message: '恭喜你，这是一条成功消息',
          type: 'success'
        });
        }
      })
    }
  },
};
</script>

